import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot } from '@angular/router';
import { ContextDashboardType } from '../context-dashboard.model';
import { ContextDashboardService } from '../context-dashboard.service';
import { tap } from 'rxjs/operators';
import { from, Observable, of } from 'rxjs';
import { Tab } from '@c8y/ngx-components';

@Injectable()
export class ReportDashboardGuard {
  constructor(private contextDashboardService: ContextDashboardService) {}

  canActivate(route: ActivatedRouteSnapshot): Observable<boolean | Tab[]> {
    const reportId = route.data.contextData && route.data.contextData.id;
    return from(
      this.contextDashboardService.activateDashboards(route, [ContextDashboardType.Named])
    ).pipe(
      tap(res => {
        return Array.isArray(res) && res.length === 0
          ? this.createDashboardIfNotExist(reportId)
          : of(true);
      })
    );
  }

  createDashboardIfNotExist(reportId) {
    const dashboardCfg = {};
    return this.contextDashboardService
      .create(dashboardCfg, undefined, `report_${reportId}`)
      .then(() => true);
  }
}
