import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { ViewContext, ActionBarFactory, ActionBarItem } from '@c8y/ngx-components';
import { Observable } from 'rxjs';
import { ContextDashboardService } from './context-dashboard.service';
import { PasteDashboardActionComponent } from './paste-dashboard-action.component';

@Injectable({
  providedIn: 'root'
})
export class DashboardActionBarFactory implements ActionBarFactory {
  routeChanges$: Observable<any>;
  constructor(private contextDashboardService: ContextDashboardService) {}
  get(activeRoute: ActivatedRoute) {
    const actions = [];
    const data =
      !activeRoute.parent || activeRoute.snapshot.data.context
        ? activeRoute.snapshot.data
        : activeRoute.parent.snapshot.data;
    const { contextData } = data;
    const canPaste =
      contextData &&
      [ViewContext.Device, ViewContext.Group].includes(data.context) &&
      this.contextDashboardService.copyClipboard;

    if (canPaste) {
      actions.push({
        priority: -10,
        placement: 'more',
        template: PasteDashboardActionComponent
      } as ActionBarItem);
    }

    return actions;
  }
}
