import { Injectable } from '@angular/core';
import { ActivatedRoute } from '@angular/router';
import { IIdentified } from '@c8y/client';
import {
  ContextData,
  ContextRouteService,
  ExtensionFactory,
  Permissions,
  Tab,
  ViewContext
} from '@c8y/ngx-components';
import { BsModalService } from 'ngx-bootstrap/modal';
import { AddDashboardComponent } from './add-dashboard.component';
import { ContextDashboardManagedObject } from './context-dashboard.model';
import { ContextDashboardService } from './context-dashboard.service';
import { DashboardDetailComponent } from './dashboard-detail.component';

@Injectable({
  providedIn: 'root'
})
export class AddDashboardFactory implements ExtensionFactory<Tab> {
  currentContext: ContextData;

  constructor(
    private permissions: Permissions,
    private contextDashboardService: ContextDashboardService,
    private bsModal: BsModalService,
    private contextRoute: ContextRouteService
  ) {}

  async get(activatedRoute?: ActivatedRoute): Promise<Tab | Tab[]> {
    this.currentContext = this.contextRoute.getContextData(activatedRoute);
    if (
      [ViewContext.Device, ViewContext.Group].includes(this.currentContext?.context) &&
      (await this.hasPermission(this.currentContext.contextData))
    ) {
      return [
        {
          component: AddDashboardComponent,
          priority: -Infinity
        }
      ];
    }
    return [];
  }

  async addDashboard() {
    let initialState: { deviceType: string };

    if (this.currentContext.context === ViewContext.Device) {
      initialState = {
        deviceType: this.currentContext.contextData.type
      };
    }

    const modal = this.bsModal.show(DashboardDetailComponent, {
      class: 'modal-lg',
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      initialState,
      ignoreBackdropClick: true
    }).content as DashboardDetailComponent;
    try {
      const dashboardCfg = await modal.result;
      const dashboardMO: ContextDashboardManagedObject = await this.contextDashboardService.create(
        dashboardCfg,
        this.currentContext
      );
      await this.contextDashboardService.navigateToDashboard(dashboardMO);
      modal.close();
    } catch (ex) {
      // intended empty
    }
  }

  private async hasPermission(context: IIdentified) {
    if (context?.id) {
      return await this.permissions.canEdit(
        ['ROLE_INVENTORY_ADMIN', 'ROLE_INVENTORY_CREATE'],
        context
      );
    }
    return this.permissions.hasAnyRole(['ROLE_INVENTORY_ADMIN', 'ROLE_INVENTORY_CREATE']);
  }
}
