import { Component, Inject, Optional } from '@angular/core';
import { AppStateService, gettext } from '@c8y/ngx-components';
import {
  CockpitDashboardConfig,
  COCKPIT_HOME_DASHBOARD_CONFIG,
  DEFAULT_COCKPIT_HOME_WIDGETS
} from './cockpit-dashboard.model';

@Component({
  selector: 'c8y-cockpit-dashboard',
  templateUrl: './cockpit-dashboard.component.html'
})
export class CockpitDashboardComponent {
  defaultWidgets = DEFAULT_COCKPIT_HOME_WIDGETS;
  dashboardName = 'home-cockpit1';
  pageTitle: string = gettext('Home');

  constructor(
    private appState: AppStateService,
    @Optional()
    @Inject(COCKPIT_HOME_DASHBOARD_CONFIG)
    private homeDashboardConfig: CockpitDashboardConfig
  ) {
    const config = {
      ...this.homeDashboardConfig,
      dashboardName: appState.currentApplication.value?.config?.homeDashboardName,
      userSpecific: appState.currentApplication.value?.config?.userSpecificHomeDashboard
    };
    this.applyDashboardConfig(config);
  }

  private applyDashboardConfig(config: CockpitDashboardConfig) {
    if (!config) {
      return;
    }

    if (config.dashboardName) {
      this.dashboardName = config.dashboardName;
    }

    if (config.defaultWidgets) {
      this.defaultWidgets = config.defaultWidgets;
    }

    if (config.userSpecific && this.appState.currentUser.value) {
      this.dashboardName = `${this.appState.currentUser.value.userName.replace(/\./g, '__')}_${
        this.dashboardName
      }`;
    }

    if (config.pageTitle) {
      this.pageTitle = config.pageTitle;
    }
  }
}
