import { Injectable } from '@angular/core';
import { ActivatedRouteSnapshot } from '@angular/router';
import { Observable } from 'rxjs';
import { Tab } from '@c8y/ngx-components';
import { ContextDashboardType } from './context-dashboard.model';
import { ContextDashboardService } from './context-dashboard.service';

@Injectable()
export class GroupDashboardGuard {
  constructor(private contextDashboardService: ContextDashboardService) {}

  canActivate(route: ActivatedRouteSnapshot): Observable<boolean | Tab[]> {
    return this.contextDashboardService.activateDashboards(route, [ContextDashboardType.Group]);
  }
}
