import { Component, OnInit } from '@angular/core';
import { gettext, Widget } from '@c8y/ngx-components';
import { WidgetService } from '../widget.service';

@Component({
  selector: 'c8y-device-management-home-dashboard',
  templateUrl: './device-management-home-dashboard.component.html'
})
export class DeviceManagementHomeDashboardComponent implements OnInit {
  widgets = [];
  dashboardName = 'home-dm1';
  defaultWidgets = [
    {
      name: 'devicemanagement.welcome.widget',
      title: gettext('Quick links'),
      _x: 0,
      _y: 0,
      _height: 4,
      _width: 6
    },
    {
      name: 'Recent Alarms',
      title: gettext('Recent alarms'),
      _x: 6,
      _y: 0,
      _height: 4,
      _width: 6
    }
  ] as Widget[];

  constructor(private widgetService: WidgetService) {}

  ngOnInit() {
    this.defaultWidgets.map(async widget => {
      await this.widgetService
        .mapLegacy(widget)
        .then(transformedWidget => this.widgets.push(transformedWidget));
    });
  }
}
