import { Component, OnInit, ViewChild } from '@angular/core';
import { BehaviorSubject, combineLatest, Observable } from 'rxjs';
import { BsModalService } from 'ngx-bootstrap/modal';
import { IManagedObject, InventoryService, IResultList } from '@c8y/client';
import { debounceTime, distinctUntilChanged, shareReplay, switchMap, tap } from 'rxjs/operators';
import {
  FilterInputComponent,
  gettext,
  ModalService,
  Status,
  AlertService,
  Permissions
} from '@c8y/ngx-components';
import { DashboardDetailComponent } from '../dashboard-detail.component';
import { ContextDashboardService } from '../context-dashboard.service';
import { ContextDashboardType } from '../context-dashboard.model';
import { TranslateService } from '@ngx-translate/core';
import { ReportDashboardService } from './report-dashboard.service';

@Component({
  selector: 'c8y-report-dashboard-list',
  templateUrl: './report-dashboard-list.component.html'
})
export class ReportDashboardListComponent implements OnInit {
  @ViewChild(FilterInputComponent, { static: false }) filter: FilterInputComponent;
  textFilter$: BehaviorSubject<string> = new BehaviorSubject('');
  reload$: BehaviorSubject<void> = new BehaviorSubject(null);
  reloading = false;
  reports$: Observable<IResultList<IManagedObject>> = combineLatest(
    this.textFilter$.pipe(debounceTime(400), distinctUntilChanged()),
    this.reload$
  ).pipe(
    tap(() => {
      this.reloading = true;
    }),
    switchMap(([text]) => this.loadReports(text)),
    tap(() => {
      this.reloading = false;
    }),
    shareReplay(1)
  );
  canAddReport = false;
  private readonly DELETED_SUCCESS_MSG = gettext('Report deleted.');

  constructor(
    private inventoryService: InventoryService,
    private contextDashboardService: ContextDashboardService,
    private bsModal: BsModalService,
    private translateService: TranslateService,
    private modal: ModalService,
    private alertService: AlertService,
    private reportDashboardService: ReportDashboardService,
    private permissions: Permissions
  ) {}

  ngOnInit() {
    this.canAddReport = this.permissions.hasAnyRole([
      'ROLE_INVENTORY_CREATE',
      'ROLE_INVENTORY_ADMIN'
    ]);
  }

  async loadReports(partialName?: string) {
    return partialName
      ? this.reportDashboardService.listReports({ filter: { name: `*${partialName}*` } })
      : this.reportDashboardService.listReports();
  }

  async add() {
    const initialState = {
      isReport: true,
      isNamedDashboard: true
    };
    const modal = this.bsModal.show(DashboardDetailComponent, {
      class: 'modal-lg',
      ignoreBackdropClick: true,
      ariaDescribedby: 'modal-body',
      ariaLabelledBy: 'modal-title',
      initialState
    }).content as DashboardDetailComponent;
    try {
      const cfg = await modal.result;
      const { name, icon, c8y_IsNavigatorNode, priority, description, ...dashboardCfg } = cfg;
      const report = (
        await this.reportDashboardService.createReport({
          name,
          icon,
          c8y_IsNavigatorNode,
          priority,
          description
        } as Partial<IManagedObject>)
      ).data;

      await this.contextDashboardService.create(
        dashboardCfg,
        undefined,
        `${this.contextDashboardService.REPORT_PARTIAL_NAME}${report.id}`
      );
      if (report.c8y_IsNavigatorNode) {
        this.reportDashboardService.addReportNavigatorNode(report);
      }
      this.reload$.next();
      modal.close();
    } catch (ex) {
      if (ex) {
        throw new Error(`Something went wrong: ${ex}`);
      }
    }
  }

  async delete(report: IManagedObject) {
    try {
      const dashboard = await this.contextDashboardService
        .getDashboard$(`report_${report.id}`, [ContextDashboardType.Named])
        .toPromise();
      const isDashboardFrozen = !!dashboard?.c8y_Dashboard?.isFrozen;

      const msg = isDashboardFrozen
        ? gettext(
            `You are about to delete the report "{{ reportName }}". Be aware that the report has a locked dashboard assigned. Do you want to proceed?`
          )
        : gettext(`You are about to delete the report "{{ reportName }}". Do you want to proceed?`);

      await this.modal.confirm(
        gettext('Delete report'),
        this.translateService.instant(msg, {
          reportName: report.name
        }),
        Status.DANGER,
        {
          ok: gettext('Delete'),
          cancel: gettext('Cancel')
        }
      );

      await this.contextDashboardService.delete(dashboard, false);
      await this.inventoryService.delete(report.id);
      this.alertService.success(this.DELETED_SUCCESS_MSG);
      if (report.c8y_IsNavigatorNode) {
        this.reportDashboardService.removeNavigatorNode(report);
      }
      this.reload$.next();
    } catch (ex) {
      if (ex) {
        throw new Error(`Something went wrong: ${ex}`);
      }
    }
  }

  async update(report: IManagedObject) {
    report.c8y_IsNavigatorNode = report.c8y_IsNavigatorNode ? {} : null;
    await this.inventoryService.update(report);
    report.c8y_IsNavigatorNode
      ? this.reportDashboardService.addReportNavigatorNode(report)
      : this.reportDashboardService.removeNavigatorNode(report);
  }
}
